* {
  padding: 0;
  margin: 0;
  //box-sizing: border-box;
}

:root {
  --yelloow-yellow: #FFBF00;
  --yelloow-green: #4BBF73;
  --yelloow-red: #D9534F;
  --yelloow-darkgray: #9497A1;

  --yelloow-yellow-gradient: linear-gradient(0deg, rgba(255, 191, 0, 0.85) 0%, rgba(255, 191, 0, 1) 100%);
  --yelloow-green-gradient: linear-gradient(0deg, rgba(75, 191, 115, 0.85) 0%, rgba(75, 191, 115, 1) 100%);;
  --yelloow-red-gradient: linear-gradient(0deg, rgba(217, 83, 79, 0.85) 0%, rgba(217, 83, 79, 1) 100%);
}

h1 {
  font-size: 15px;
}

h2 {
  margin: 0 0 0 15px;
}

p {
  margin: 0;
  font-size: 13px;
  color: var(--yelloow-darkgray);
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  align-items: start;

  width: 100%;
  height: 100%;
}

.wrapper {
  width: 100%;
}

.header {
  //margin-bottom: 15px;
  margin: 15px 0 15px 15px;
}

.faller-chart {
  display: flex;
  justify-content: space-between;
  align-items: center;


}

.costs {
  display: flex;
  justify-content: start;

  color: var(--yelloow-red);

  background: linear-gradient(0deg, rgba(217, 83, 79, 0.1909357492997199) 0%, rgba(217, 83, 79, 0.24135591736694673) 100%);

  padding: 5px;
  border-radius: 5px;
  margin: 5px 0px 5px 20px;
}

.text {
  list-style-type: disc;
  width: 100%;
}

.theme-yellow-top {
  background: var(--yelloow-yellow-gradient)
}

.theme-yellow-bottom {
  background: var(--yelloow-yellow);
}

.theme-red-top {
  background: var(--yelloow-red-gradient)
}

.theme-red-bottom {
  background: var(--yelloow-red);

}

.theme-green-top {
  background: var(--yelloow-green-gradient);
}

.theme-green-bottom {
  background: var(--yelloow-green);
}